import request from '@/static/request.js'
export default {
  data() {
    return {
      userId: 0,
      searchContent: '',
      array: ['姓名','手机','微信'],
      index: 0,
      schoolmates: [],
    };
  },
  methods: {
    search() {
      const vm = this;
      request
        .get('selectSchoolmate/', {
          type: vm.array[vm.index],
          content: vm.searchContent,
        })
        .then(res => {
          vm.schoolmates = res.data
        })
      uni.showToast({
        title: '搜索成功！',
        duration: 1000,
      })
    },
    showIntro(a) {
      let temp = this;
      uni.setStorage({
        key: 'StudentInfo',
        data: a
      });
      uni.navigateTo({
        url: '../schoolmates/student'
      });
    },
    classChange: function(e) {
      this.index = e.target.value
    }
  },
  mounted() {
    const vm = this
    request
      .get('User', {
        
      })
      .then(res => {
        this.schoolmates = res.data
      })
  },
};
